import React, { useState } from 'react';

import Kanban from './src/Kanban';

import { Provider } from 'react-redux';
import store from './src/Utils/store';

const App = () => {
  const [isFirst, setIsFirst] = useState(true);
  return (
    <>
      {isFirst && (
        <div div className='modal-high'>
          <div className='modal' onClick={() => setIsFirst(!isFirst)}>
            <h3>What is a Kanban board?</h3>
            <p>
              A Kanban board is a visual project management tool used to track
              the progress of work items as they move through various stages of
              a workflow. It typically consists of a board with columns
              representing different stages of the workflow and cards
              representing individual tasks or work items.
            </p>
            <h3>How does a Kanban board work?</h3>
            <p>
              A Kanban board works by visualizing the flow of work items through
              a workflow. Each column on the board represents a different stage
              of the workflow, such as "To Do", "In Progress", and "Done". Work
              items are represented by cards, which are moved from one column to
              the next as they progress through the workflow. The board provides
              a visual overview of the current status of all work items, making
              it easy to identify bottlenecks and prioritize work.
            </p>
            <h3>Click on the "Kanban Board" to edit it.</h3>
          </div>
        </div>
      )}
      <Provider store={store}>
        <Kanban />
      </Provider>
      {!isFirst && (
        <div className='question' onClick={() => setIsFirst(!isFirst)}>
          ?
        </div>
      )}
      <div className='outro'>
        Made with ❤️ by&nbsp;
        <a href='https://dhawal-pandya.github.io/'>Dhawal Pandya</a>
      </div>
    </>
  );
};

export default App;
